import "core-js/modules/es.array.push.js";
import { mapGetters } from 'vuex';
export default {
  name: 'CustomerConfig',
  data() {
    return {
      loading: false,
      tableData: {
        total: 0,
        page: 1,
        pageSize: 15,
        disabledPage: false
      },
      dealedData: [],
      // 根据IP地址处理后的数据，格式为 {ip, count, [data]}
      expandRowKeys: []
    };
  },
  computed: {
    ...mapGetters(['credentials']),
    disableTooltip() {
      return window.innerWidth < 768;
    }
  },
  methods: {
    handlePageChange(page, row) {
      row.page = page;
      this.getList(row);
    },
    handlePageSizeChange(pageSize, row) {
      row.pageSize = pageSize;
      this.getList(row);
    },
    handlePageChange2(page) {
      this.tableData.page = page;
      this.getIpGroupList();
    },
    handlePageSizeChange2(pageSize) {
      this.tableData.pageSize = pageSize;
      this.getIpGroupList();
    },
    getIpGroupList() {
      const params = this.tableData;
      let url = "https://studyjava.cn/api/cloudflare/files/ip";
      //拼接参数
      let paramsArray = [];
      Object.keys(params).forEach(key => {
        if (params[key]) {
          if (key == 'pageSize') {
            key = 'size';
          }
          if (params[key]) {
            paramsArray.push(key + '=' + params[key]);
          }
        }
      });
      if (url.search(/\?/) === -1) {
        url += '?' + paramsArray.join('&');
      } else {
        url += '&' + paramsArray.join('&');
      }
      fetch(url, {
        method: 'GET'
      }).then(response => response.json()).then(result => {
        this.dealedData = this.dealByIP(result.data); // 根据IP地址处理数据
      }).catch(err => {
        if (err.message !== 'Unauthorized') {
          this.$message.error('同步数据时出错，请检查网络连接' + err.message);
        }
      });
    },
    getList(row) {
      if (!row.expand) {
        this.loading = true;
        row.disabledPage = true;
        this.fetchWithAuth("/api/manage/list?ip=" + row.ip + "&page=" + row.page + "&pageSize=" + row.pageSize, {
          method: 'GET'
        }).then(response => response.json()).then(result => {
          row.data = result.data; // 根据IP地址处理数据
          row.expand = true;
          row.disabledPage = false;
          this.loading = false;
        }).catch(err => {
          this.loading = false;
          if (err.message !== 'Unauthorized') {
            this.$message.error('同步数据时出错，请检查网络连接');
          }
        });
      }
    },
    getRowKeys: function (row) {
      return row.id;
    },
    expandRow(row, expandedRows) {
      if (expandedRows.length > 0) {
        this.expandRowKeys = [];
        if (row) {
          this.expandRowKeys.push(row.id);
          this.getList(row);
        }
      } else {
        this.expandRowKeys = [];
      }
    },
    async fetchWithAuth(url, options = {}) {
      // 开发环境, url 前面加上 /api
      // url = `/api${url}`;
      if (this.credentials) {
        // 设置 Authorization 头
        options.headers = {
          ...options.headers,
          'Authorization': `Basic ${this.credentials}`
        };
        // 确保包含凭据，如 cookies
        options.credentials = 'include';
      }
      const response = await fetch(url, options);
      if (response.status === 401) {
        // Redirect to the login page if a 401 Unauthorized is returned
        this.$message.error('认证状态错误，请重新登录');
        this.$router.push('/adminLogin');
        throw new Error('Unauthorized');
      }
      return response;
    },
    dealByIP(data) {
      let dealedData = [];
      this.tableData.total = data.total;
      data.records.forEach(item => {
        let ip = item?.UploadIP;
        let count = item?.count;
        dealedData.push({
          id: ip,
          ip,
          count,
          total: count,
          page: 1,
          pageSize: 15,
          disabledPage: false,
          data: []
        });
      });
      return dealedData;
    },
    handleGoUpload() {
      this.$router.push('/');
    },
    handleLogout() {
      this.$store.commit('setCredentials', null);
      this.$router.push('/adminLogin');
    },
    handleGoHome() {
      this.$router.push('/dashboard');
    },
    formatTimeStamp(timeStamp) {
      return new Date(timeStamp).toLocaleString();
    },
    sortByTimestamp(a, b) {
      return new Date(a.metadata.TimeStamp) - new Date(b.metadata.TimeStamp);
    }
  },
  mounted() {
    this.fetchWithAuth("/api/manage/check", {
      method: 'GET'
    }).then(response => response.text()).then(result => {
      if (result == "true") {
        this.showLogoutButton = true;
        // 在 check 成功后再执行 list 的 fetch 请求
        this.getIpGroupList();
      } else if (result == "Not using basic auth.") {
        this.getIpGroupList();
      } else {
        throw new Error('Unauthorized');
      }
    }).catch(err => {
      if (err.message !== 'Unauthorized') {
        this.$message.error('同步数据时出错，请检查网络连接' + err.message);
      }
    });
  }
};